class SimsCods4 extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activeView: 'home',
			activePanel: 'page1',
			textView1: 'false',
			snackBar: null
		}

		this.setFaq = this.setFaq.bind(this);
		this.copyCode = this.copyCode.bind(this);
	}

	setFaq () {
		if(this.state.textView1 == 'faq1') {
			this.setState({textView1: 'not'});
		} else {
			this.setState({textView1: 'faq1'});
		}
	}

	copyCode(codes) {
		let co = SimsCods4.map(obj => {
			if (obj.id == codes) {
				return obj.code;
			}
		});

		function isNumber(num) {
			return typeof num === 'number' && !isNaN(num);
		}

		if (isNumber(co)) {
			this.setState({
				snackBar:
					<Snackbar
						onClose={() => this.setState({ snackBar: null })}
						action="Закрыть"
						onActionClick={() => this.setState({ snackBar: null })}
						before={<Avatar size={24} style={{ "background-color": 'white' }}><Icon24ErrorCircle fill="red" width={24} height={24} /></Avatar>}
					>
						Код не существует или был удалён!
					</Snackbar>
			});
		} else {
			bridge.send('VKWebAppCopyText', { text: co })
				.then(data => {
					this.setState({
						snackBar:
							<Snackbar
								onClose={() => this.setState({ snackBar: null })}
								action="Закрыть"
								onActionClick={() => this.setState({ snackBar: null })}
								before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon16Done fill="#fff" width={24} height={24} /></Avatar>}
							>
								Код скопирован.
							</Snackbar>
					});
				})
				.catch(e => {
					this.setState({
						snackBar:
							<Snackbar
								onClose={() => this.setState({ snackBar: null })}
								action="Закрыть"
								onActionClick={() => this.setState({ snackBar: null })}
								before={<Avatar size={24} style={{ background: 'var(--accent)' }}><Icon24ErrorCircle fill="#fff" width={24} height={24} /></Avatar>}
							>
								Ошибка метода <b>VKWebAppCopyText</b>! <br />
								Номер ошибки: {e}
							</Snackbar>
					});
				});
		}
	};

	render () {
		return (
			<ConfigProvider>
				<AdaptivityProvider>
					<AppRoot>
						<SplitLayout header={<PanelHeader separator={false} />}>
							<SplitCol>
								<Root activeView={this.state.activeView}>
									<View id="home" activePanel={this.state.activePanel}>
										<Panel id="page1">
											<PanelHeader> Коды </PanelHeader>

											<FixedLayout filled vertical="top">
													<Search placeholder="Введите название кода" />
											</FixedLayout>
											
											<Spacing size={45} />

											<Banner
												before={<Icon16WarningTriangle fill="red" width={25} height={25} />}
												text="Имейте ввиду, что некоторые коды будут работать только после включения режима разработчика."
												actions={
												<React.Fragment>
													<Button onClick={() => this.copyCode("1")} style={{"background-color": "#269e24"}} before={<Icon20CopyOutline width={22} height={22} />} size="m">Скопировать код разработчика</Button>
												</React.Fragment>
												}
											/>

											<Group mode="plain" separator="hide" style={{ "margin-left": "10px", "margin-right": '10px', "text-align": 'left', "border-radius": "8px", "background-color": "#d0facf" }} header={<Link onClick={this.setFaq} style={{ "font-weight": 'bold', "padding": '5px', "text-align": 'left', "color": "#269e24" }}>Как вводить коды?</Link>}>
												{this.state.textView1 == 'faq1' && <Text style={{ "white-space": "nowrap", "padding-left": "10px", "padding-right": "10px", "font-size": "17px" }}>
												Коды в The Sims 4 вводятся в специальной строке, которая называется <b>консолью</b>. <br/>
												Чтобы её вызвать необходимо на клавиатуре зажать сочетание клавиш <b>Ctrl + Shift + C</b>. <br/>
												Большая часть кодов вводится в режиме жизни, поэтому не стоит вводить их в начальном экране — сначала зайдите в свою семью. <br/>
												Кроме того, вам не нужно вводить коды вручную — вы можете скопировать его нажав на <Icon20CopyOutline style={{"display": "inline-block"}} width={17} height={17} fill="#269e24" />, а затем перейдя в игру и открыв консоль вставить, нажав <b>Ctrl + V</b>.
												</Text>}
											</Group>
											
											

										</Panel>
									</View>

									<View id="more" activePanel={this.state.activePanel}>
										<Panel id="page1">
											<PanelHeader> Ещё </PanelHeader>

											<Group>
												<Footer>Версия аппа: 1.0.0</Footer>
											</Group>
										</Panel>
									</View>
								</Root>
								<Tabbar>
									{this.state.snackBar}

									{(() => {
										if(this.state.activeView === 'home') {
											return <React.Fragment>
												<TabbarItem style={{"color": "#269e24"}} selected={this.state.activeView === 'home'} onClick={() => this.setState({ activeView: 'home', activePanel: 'page1' })} text={<b>Главная</b>}>
													<Icon28HomeOutline />
												</TabbarItem>

												<TabbarItem selected={this.state.activeView === 'more'} onClick={() => this.setState({ activeView: 'more', activePanel: 'page1' })} text="Ещё">
													<Icon28More />
												</TabbarItem>
											</React.Fragment>;
										} else {
											return <React.Fragment>
												<TabbarItem selected={this.state.activeView === 'home'} onClick={() => this.setState({ activeView: 'home', activePanel: 'page1' })} text="Главная">
												<Icon28HomeOutline />
												</TabbarItem>

												<TabbarItem style={{"color": "#269e24"}} selected={this.state.activeView === 'more'} onClick={() => this.setState({ activeView: 'more', activePanel: 'page1' })} text={<b>Ещё</b>}>
													<Icon28More />
												</TabbarItem>
											</React.Fragment>;
										}
									})()}

									

									
								</Tabbar>
							</SplitCol>
						</SplitLayout>
					</AppRoot>
				</AdaptivityProvider>
			</ConfigProvider>
		)
	}
}

